	async purchaseCredit(root, req, ctx){
		try {
			if(ctx.user.role !== 'Agent' ) return { title: 'Not Authenticated', content: 'Please try again.', status: 'danger' };
			await iap.setup()
			const response = await iap.validate(iap.APPLE, req.receipt)
			if(!iap.isValidated(response)) return { title: 'Error', content: 'Please try again later.', status: 'danger' }
			const purchaseDataList = iap.getPurchaseData(response)
			console.log(purchaseDataList)

			let newPurchase = false
			let agentCreditBalance = 0

			for( let purchase of purchaseDataList ) {
				const Duplicated = await Receipt.findOne({ ID: purchase.transactionId })
				if(Duplicated) continue
				const product = await PriceRate.findOne({ item: purchase.productId })
				const agent = await Agent.findOneAndUpdate({ _id: mongoose.Types.ObjectId(ctx.user._id) }, { $inc: { 'credit.balance': product.credit } }, { new: true })
				await Receipt.create({
					ID: purchase.transactionId,
					Agent: ctx.user._id,
					platform: req.platform,
					data: req.receipt,
					usd: product.usd,
					credit: product.credit,
					item: product.item,
					createdAt: moment()
				})
				await Transaction.create({
					ID: purchase.transactionId,
					Agent: ctx.user._id, 
					type: 'Iap',
					description: `Purchased ${product.credit} credit ($${product.usd})`,
					createdAt: moment(),
					amount: product.credit,
					balance: agent.credit.balance
				})
				await SystemLog.create({ title: 'Credit Purchase Success', content: `${ctx.user.username} purchased credit ${req.item} ${req.platform} ${purchase.transactionId}`, status: 'success'});
				newPurchase = true
				agentCreditBalance = agent.credit.balance
				return
			}
			if(newPurchase) return { title: 'success', content: agentCreditBalance, status: 'success' };
			return { title: 'Duplicated', content: 'Please try again later.', status: 'warning' }
		}catch(e){
			await SystemLog.create({ title: 'Credit Purchase Failed', content: `${ctx.user.username} purchased credit ${req.item} ${req.platform} ${req.receipt} ${e}`, status: 'danger'});
			return { title: 'Error', content: 'Please try again later.', status: 'danger' };
		}
	},
